import React, { Component } from "react";
import { NavLink, withRouter } from "react-router-dom";
import "./Navi.css";

class Navi extends Component {
  render() {
    var map = {
      "/about": "",
      "/knowledge": "",
      "/portfolio": "",
      "/resume": "",
      "/contact": ""
    };

    var pathname = this.props.location.pathname;
    pathname = pathname === "/" ? "/about" : pathname;

    map[pathname] += "current-content";

    return (
      <nav className="clearfix">
        <ul className="site-nav">
          <li>
            <NavLink to="/about" className={map["/about"]}>
              About
            </NavLink>
          </li>
          <li>
            <NavLink to="/resume" className={map["/resume"]}>
              Resume
            </NavLink>
          </li>
          <li>
            <a href="/biboroku">Blog</a>
          </li>
          <li>
            <NavLink to="/knowledge" className={map["/knowledge"]}>
              Knowledge
            </NavLink>
          </li>
          <li>
            <NavLink to="/portfolio" className={map["/portfolio"]}>
              Portfolio
            </NavLink>
          </li>
          <li>
            <NavLink to="/contact" className={map["/contact"]}>
              Contact
            </NavLink>
          </li>
        </ul>
      </nav>
    );
  }
}

export default withRouter(Navi);
